                <h2 class="bold">The worksheet: the core of DSCVR</h2>
                <p>While working on the DSCVR team at 1010data, I was given the task of redesigning the lion's share of the application – the worksheet – and all it's components. Being a new designer, this task was daunting as it was the widest and most flexible part of the application. However, looking back, I am extremely honored to have had the opportunity.</p>
                <h3>The timeline and the view</h3>
                <p>The DSCVR worksheet is the primary interface in which a user can interact with data in DSCVR. It importance is analogous to the sheet in Microsoft Excel, or the canvas in Adobe Photoshop. </p>
                <p>In the worksheet, a user can string together operations, which are blocks of code that manipulates the dataset in a particular way, and execute them sequentially by 'running' the timeline. This could result in a query that can be as simple as computing a new column, to as complex as running grouped modelling and regression.</p>
                <p>There a two main parts to the DSCVR worksheet, namely:
                <ul>
                <li>The timeline – it occupies the left section of the worksheet and contains a list of operations that build up a query.</li>
                <li>The view – the results of a timeline run is displayed in this right section of the worksheet.</li>
                </ul>
                </p>
            </div>
