            <h2 class="bold">Understanding the quirks of our technology</h2>
            <p>
                DSCVR is powered by the core of 1010data's technology &mdash; EDGE and data imported into DSCVR is stored on a propietary database called EDGEdb. In this database, tables are broken up into segments and some <em>complex tech-magic</em> takes place that allows EDGE to fetch and process data from EDGEdb at super high speeds.
            </p>
            <p>
                The problem here is that the configuration of this <em>complex tech-magic</em> has not been automated and often requires a user to learn and understand our technology, or get help from someone who does.
                The ideal solution here would have been to automate this process, however, that required a significant effort that we were not resourced to undertake. Hence, until that happens, a DSCVR user has to be able to navigate the data import tool on their own.
            </p>
            <p>
                To fully understand our technology and map out its quirks, I dug into my engineering days and created an entity-relationship (ER) diagram. This allowed me to visualize the whole process and identify opportunities to abstract complexity.
            </p>
            </div>
        </section>
        <section class="page-section span-row">
