            <h2 class="bold">Challenge: Distilling the workflow down to its basics</h2>
            <p>
                Taking the ER diagram I created, I identified the core components that absolutely required user input/validation. They were:
                <ol>
                    <li>Data source: "Where is your data currently?"</li>
                    <li>Configuration of columns: "How should DSCVR store each column of your table?"</li>
                    <li>Configuration of the table: "How should DSCVR store your table? And who can access it?"</li>
                </ol>
            </p>
            <h3>1. Data source</h3>
                <p>This was the very first question that needed to be answered in the data import workflow. As the list was rather big and expected to grow, I created two variations to solve this.
                The first was a list, and the second was a card grid. The list version had the benefit of quick scanning, but we found that the logos of the data sources were reconigable enough that it sped up the first step.</p>
