                caption="The supported data sources displayed in a alphabetized card grid."/>
                </div>
                <p>Despite being faster, we saw a few users spending longer times at this step and added a search field and a 'Popular data sources' category. This reduced friction for these users at this step.</p>
                <Figure class="section-spacer" imgSource="/assets/DSCVR/data-import-step1-final.png" figureLocation="above" 
