            <h3>2. Configuration of columns</h3>
                <p>After selecting a data source, the user has to validate that the data will be brought into DSCVR correctly.</p>
                <p>In the first version of the data import workflow, we asked the user to validate the file parsing settings and we presented the user with a partially editable grid that the user can use to modify a column's configuration.
                From our first beta, we realized that the addition of editable fields in the grid forced the columns to be wider, and also made the grid harder to read. 
                </p>
                <p>We also noticed that most people skip both of these steps because their data looks correct.
                Hence in the new data import workflow, I decided to present users with a non-editable grid that reveals an edit button on hover of a column.
                </p>
                <Figure class="section-spacer" imgSource="/assets/DSCVR/data-import-step2-grid.png" figureLocation="above" 
