import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router';
import Home from './views/Home.vue'
import discoPage from './views/work-pages/disco.vue'
import dscvrPage from './views/work-pages/dscvr.vue'
import dscvrHomepage from './views/work-pages/dscvr-homepage.vue'
import dscvrDataImportPage from './views/work-pages/dscvr-data-import.vue'
import dscvrWorksheetPage from './views/work-pages/dscvr-worksheet.vue'
import brofPage from './views/work-pages/brof.vue'
import brofFilteringPage from './views/work-pages/brof-filtering.vue'
import wayfinderPage from './views/work-pages/wayfinder.vue'
import gsProjectPage from './views/work-pages/gs-project.vue'
import PageNotFound from './views/404.vue'
Vue.config.productionTip = false


Vue.use(VueRouter)

const routes = [
  { path: '', component: Home },
  { path: '/work/disco', component: discoPage },
  { path: '/work/dscvr', component: dscvrPage },
  { path: '/work/dscvr-homepage', component: dscvrHomepage },
  { path: '/work/dscvr-data-import', component: dscvrDataImportPage },
  { path: '/work/dscvr-worksheet', component: dscvrWorksheetPage },
  { path: '/work/brof', component: brofPage },
  { path: '/work/brof-filtering', component: brofFilteringPage },
  { path: '/work/corporate-wayfinder', component: wayfinderPage },
  { path: '/work/gs-project', component: gsProjectPage },
  { path: '/404', component: PageNotFound },
  { path: '*', redirect: '/404' },
]

const router = new VueRouter({
  mode: 'history',
  routes, // short for `routes: routes`,
  scrollBehavior: function(to) {
    if (to.hash) {
        return {selector: to.hash}
    } else {
        return { x: 0, y: 0 }
    }
  }
})

new Vue({
  el: '#app',
  router,
  render: h => h(App)
}).$mount('#app')

