          <p>While preparing for Disco, I remembered an issue that we have been facing in our design process, which was caused by differing opinions; designers were optimizing for different experiences. I knew this topic was inadvertantly going to come up again with a project like Disco, so I decided to create some design principles. These tenets will guide us in making tough decisions and keep us true to our vision.</p>
          <p>To understand the values that we embody (and wish to embody) as a company, I scheduled informal chats with about 20 colleagues and stakeholders to talk about our users, our products, and our company. I combined their feedback with the insights from past user research, and distilled all of it down to 5 principles.</p>
          <Figure imgSource="/assets/Design-Principles/design-principles-titles.png" 
