          <p>As a company, we think that our designs should be:
            <ol class="design-principles-list">
              <li>
                <h3><img class="design-principle-icon" alt-text="" src="/assets/Design-Principles/design-principles-data-driven.png">Data-driven</h3>
                We place an enormous emphasis on research in our design process, and we do that to understand our users. Research helps us discover their needs and wants, validate our hunches and decisions, and build desirable products.
              </li>
              <li>
                <h3><img class="design-principle-icon" alt-text="" src="/assets/Design-Principles/design-principles-efficient.png">Efficient</h3>
                Our users come to us to make sense of their data, and we exist to make their work easier. We respect our users’ time, attention, and effort so we ideate on ways to make their workflow better, faster, and more delightful.
              </li>
              <li>
                <h3><img class="design-principle-icon" alt-text="" src="/assets/Design-Principles/design-principles-clear.png">Clear</h3>
                We facilitate our users’ workflow by reducing distractions and designing consistent experiences. We present only relevant information and use patterns and words that are familiar to our users.
              </li>
              <li>
                <h3><img class="design-principle-icon" alt-text="" src="/assets/Design-Principles/design-principles-friendly.png">Friendly</h3>
                We empathize and always try to meet our users at their level. As our users’ staunchest spokesperson, we do our best to guide and empower them.  We understand that mistakes still happen, so we make it easy for our users to recover from a mistake and move on with their work.              </li>
              <li>
                <h3><img class="design-principle-icon" alt-text="" src="/assets/Design-Principles/design-principles-aesthetic.png">Aesthetic</h3>
                We are intentional with every detail and aim to makle daunting data work simple and delightful. Finally, we strive to craft elegant experiences because we believe attractive things work better.
              </li>
            </ol>
          </p>
          <br>
          <p>In addition, for Disco, there are two more principles that we hold to be true:
