          <h2 class="bold">Discovering Disco's structure</h2>
          <p>
            At 1010data, we have a suite of products that we sell as is, and many other products that we create on a one-off basis for a client. 
            As Disco will serve all of these products, it needed to be prescriptive enough to maintain a consistent brand experience, but also flexible enough to allow for creativity and personalization as needed.
          </p>
          <p>
            To accomplish that, we have decided to create a handful of CSS files with variables and design tokens that basically breaks styling down to atomic components. 
            This allows a consumer of Disco to simply adjust some variables, and have their changes cascade throughout the system.
          </p>
          <p>
            As some of our products need to be embedded within a client's existing product, we have also decided to go with web components as they can be more readily inserted into any front-end framework.
            We are also using StencilJS to help us build out these web components.
          </p>
