osed that the selection list be displayed in a nested structure in the new filtering modal. This is so that a group of items could be removed simultaneously. For example, deleting the 'Asia Pacific' filter item also deletes 'South East Asia' from the filter. This also reduces clutter on the dashboard.\n            ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"page-section"},[_c('div',{},[_c('h3',{staticClass:"bold"},[_vm._v("Too many clicks to add and remove filters")]),_c('p',[_vm._v("\n                A animated filter button was used to replace the filtering icon. This allows for users to go to their target tab in the filtering modal in one click while saving space on the tool bar by showing the filter categories only upon hover. Applied filters can also be cleared quickly in the same way.\n            ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"page-section section-spacer span-row"},[_c('div',{},[_c('h2',{staticClass:"bold"},[_vm._v("Outcome")]),_c('p',[_vm._v("\n                    Looking at tracking analytics after the implementation of the proposed changes to the filtering workflow, we observed an average of 6% reduction in time-to completion for a user to apply a one-line filter. More astoundingly, the time to completion for a user to apply complex filters (defined as filters in more than one category) dropped almost 29%.\n                ")])])])}]

