
<template>
  <figure :class="['figure-container', flexDir]">
    <div class="image-container">
      <img v-if="expandable" class="image image-zoomable" :src="imgSource" @click="expandImage"/>
      <img v-else class="image" :src="imgSource"/>
    </div>
  <div v-if="caption" class="caption-container">
    <figcaption class="title-font">
        <span class="direction"></span>
        <span class="delimiter"> / </span>
        <span class="caption-text">{{ caption }}</span>
    </figcaption>
  </div>
  <transition name="fade">
    <div v-if="expandable && imageExpanded" class="image-full-screen viewport-overlay" @click="collapseImage">
      <div class="close-button" aria-label="Close image" @click="collapseImage">&times;</div>
      <img class="image" :src="imgSource" @click="e => {e.stopPropagation()}" :aria-describedby="caption">
      <div v-if="caption" class="caption-text" aria-hidden="true">{{caption}}</div>
    </div>
  </transition>
  </figure>
</template>

<script>
export default {
  name: 'Figure',
  props: {
    figureLocation: String, //Takes these values: Above, Below, Left, Right
    caption: String,
    imgSource: String,
    expandable: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      imageExpanded: false
    }
  },
  methods: {
    onEscapeKeyUp(e) {
      if (e.which === 27) {
        this.collapseImage();
      }
    },
    collapseImage(e) {
      this.imageExpanded = false
      document.querySelector('body').classList.remove('no-scroll');
      window.removeEventListener("keyup", this.onEscapeKeyUp);
      e.stopPropagation();
    },
    expandImage(e) {
      this.imageExpanded = true
      document.querySelector('body').classList.add('no-scroll');
      window.addEventListener("keyup", this.onEscapeKeyUp);
    }
  },
  computed: {
    flexDir(){
      let dir = this.figureLocation ? this.figureLocation.toLowerCase() : 'above'
      switch (dir) {
        case 'above': {
          return 'flex-column'
        }
        case 'below': {
          return 'flex-column-reverse'
        }
        case 'left': {
          return 'flex-row'
        }
        case 'right': {
          return 'flex-row-reverse'
        }
        default: {
          return 'flex-column'
        }
      }
    }
  }
}
</script>

