                <li><h4 style="margin-bottom: 0.5rem;">Lengthy, often unneccessary, steps</h4>The configuration steps in the workflow takes the most time to go through, and most users do not change the default configuration.</li>
                <li><h4 style="margin-bottom: 0.5rem;">Jargon harms more than help</h4>The words used in the import workflow though technically accurate, falsely assumes users have a high level of familiarity with databases.</li>
                <li><h4 style="margin-bottom: 0.5rem;">Too much, too quick</h4>The user is exposed to too many fields on the screen at once which makes focusing difficult.</li>
                <li><h4 style="margin-bottom: 0.5rem;">Lack of grouping makes parsing slower</h4>Coupled with the issue above, the lack of obvious grouping of fields make scanning for the important fields tideous.</li>
                <li><h4 style="margin-bottom: 0.5rem;">Data inaccuracy causes frustration</h4>Due to parsing issues, occasionally data is loaded incorrectly and must be manually corrected by a user.</li>
                <li><h4 style="margin-bottom: 0.5rem;">Large screen real estate needed</h4>The layout of the import pages were not responsive and thus painful to use on medium or smaller screens.</li>
            </ul>
