            <h2 class="bold">Understanding the quirks of our technology</h2>
            <p>As we were brainstorming solutions, we received significant pushback from engineering who cited non-trivial technological limitations. To understand the tech stack and aforementioned limitations, I scheduled a 2 hour meeting with a subject matter expert.</p>
            <p>In a nutshell, DSCVR is powered by 1010data's core technology &mdash; EDGE and data imported into DSCVR is stored on a propietary database called EDGEdb in the form of tables. These tables are broken up into segments and some <em>complex tech-magic</em> optimizes data querying speeds.</p>
            <p>The problem here is this tech-magic has not been automated and often requires a seasoned user to configure it manually.
