            <h2 class="bold">Challenge: Distilling the workflow down to its basics</h2>
            <div>
                Taking the ER diagram I created, I drew a userflow and I identified the core components that absolutely required user input/validation. They were:
                <ol>
                    <li>Data source: "Where is your data currently?"</li>
                    <li>Validation of columns: "Did DSCVR pull your data in correctly?"</li>
                    <li>Saving the table: "Where should DSCVR store your table? And who can access it?"</li>
                </ol>
                I also realized that the configuration portion was only presented because we wrongly assumed that issues with parsing were common, when that was proven to be false, know that all the users need is assurance, which can be simply acheived by skimming through a preview of their data. 
                Hence, all the configuration steps are hidden behind some user action in the new import. 
