            <h3>2. Validation of columns</h3>
                <p>After selecting a data source, the user has to configure their columns and table structure. In the first version of the data import workflow, we asked the user to configure the file parsing settings and we presented the user with a partially editable grid through which the user can modify the configuration.</p>
                <p>From our research, we realized that the addition of editable fields in the grid forced the columns to be wider, and also made the grid harder to read. Beyond that, we noticed that >90% of our users end up using the default configuration anyway, so we decided to optimize for the 'happy path'. And on this path, all the user has to do is to validate their data by quickly skiming through a table preview.</p>
                <p>Hence in the new data import workflow, we improved our parsing algorithm, and users are presented with a non-editable grid that is easily scannable, but reveals an edit button on hover of a column should they decide to modify the dafault configuration. Other parsing settings were also hidden behind a click action.</p>
                <Figure class="section-spacer" imgSource="/assets/DSCVR/data-import-step2-grid.png" figureLocation="above" 
